import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex)

export  default new vuex.Store({
    // store
    state:{
        catalogId:'',
        secondName:''
    },
    mutations:{
        // 
        setSecondName(state,n){
            state.secondName=n
        },
        // id
        setCategory(state,n){
            state.catalogId=n;
        }
    }
})


